.registration {
  display: flex;
  flex-flow: column;
  align-items: center;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  min-height: 250px;

  .next {
    width: 85%;
    height: 54px;
    margin-top: 20px;
    text-align: center;
    line-height: 54px;
    font-size: 20px;
    background-color: rgba(35, 35, 35, 0.6);
    border-radius: 20px;
    user-select: none;
    cursor: pointer;
  }
}
