.field {
  width: 85%;
  margin-bottom: 20px;

  p {
    margin-bottom: 5px;
    font-size: 12px;
    color: rgba(255, 255, 255, 0.6);
  }

  input,
  textarea,
  select {
    width: 100%;
    padding: 6px;
    border: none;
    border-radius: 8px;
    background-color: rgba(35, 35, 35, 0.6);
    outline: none;
    font-size: 20px;
    resize: none;

    &.disabled {
      color: rgb(110, 110, 110);
    }
  }
}
