@keyframes slide-right {
  to {
    left: 200%; // 50% + 150%
    opacity: 0.5;
  }
}

@keyframes slide-left {
  to {
    left: -100%; // 50% - 150%
    opacity: 0.5;
  }
}

.card {
  position: fixed;
  top: calc(50% - 54px / 2);
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-flow: column;
  width: 85%;
  height: calc(85vh - 54px);

  &.zoom-in-down,
  &.liked,
  &.disliked {
    animation-duration: 1s;
    animation-fill-mode: forwards;
  }

  &.liked {
    animation-name: slide-right;
  }

  &.disliked {
    animation-name: slide-left;
  }

  .photo {
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    position: relative;
    height: calc(100% - 44px);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 20px 20px 0 0;

    .photos-counter {
      display: flex;
      margin: 12px;

      .slide {
        width: 100%;
        height: 3px;
        margin: 0 2px;
        background-color: rgba(255, 255, 255, 0.2);
        border-radius: 20px;

        &.active {
          background-color: rgba(255, 255, 255, 0.6);
        }
      }
    }

    .left-side,
    .right-side {
      position: absolute;
      width: 50%;
      height: 100%;
    }

    .left-side {
      border-top-left-radius: 20px;
    }

    .right-side {
      right: 0;
      border-top-right-radius: 20px;
    }

    .info {
      margin: 20px;
      text-shadow: 0 0 20px black, 0 0 20px black, 0 0 20px black;

      .name {
        margin-bottom: 5px;
        font-size: 28px;
      }

      .gender,
      .target {
        font-size: 14px;
      }

      .description {
        margin-top: 5px;
        font-size: 13px;
      }
    }
  }

  .buttons {
    display: flex;
    justify-content: space-around;
    width: 100%;
    background-color: rgb(35, 35, 35);
    border-radius: 0 0 20px 20px;

    span {
      width: 100%;
      padding: 15px;
      text-align: center;
      user-select: none;
      cursor: pointer;
    }
  }
}
