@keyframes rotation {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}

.upload-photo-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin-top: 15px;
  user-select: none;

  .photo-container {
    position: relative;
    width: 150px;
    height: 200px;
    object-fit: cover;
    background-size: cover;
    background-position: center;
    border-radius: 15px;
    border: 4px solid rgb(36, 36, 36);

    .photo-icon {
      position: absolute;
      top: -2px;
      right: -2px;
      width: 30px;
      height: 30px;
      padding: 3px 0 0 4px;
      background: rgb(36, 36, 36);
      border-radius: 0 10px;
      cursor: pointer;

      .rotation {
        animation-name: rotation;
        animation-duration: 1s;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
      }
    }
  }

  .add-photo {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(36, 36, 36, 0.4);
    cursor: pointer;

    .add-icon {
      font-size: 50px;
    }
  }
}
